const headerCartBtn = document.querySelector('.user-menu .button-cart'); // кнопка в мобилке
const ipadPanelCartBtn = document.querySelector('.ipad-panel__link--cart'); // кнопка в панели для iPad
const cartPopover = document.querySelector('.cart-popover');
const cartPopoverCloseBtn = cartPopover.querySelector('.cart-popover__close');

function openCart(e) {
  e.preventDefault();
  cartPopover.classList.remove('cart-popover--closed');
  document.body.classList.add('page__container--open-modal');
}

function closeCart() {
  cartPopover.classList.add('cart-popover--closed');
  document.body.classList.remove('page__container--open-modal');
}

headerCartBtn.addEventListener('click', (e) => {
  openCart(e);
});

ipadPanelCartBtn.addEventListener('click', (e) => {
  openCart(e);
});

cartPopoverCloseBtn.addEventListener('click', () => {
  closeCart();
});

cartPopover.addEventListener('click', (e) => {
  let target = e.target;
  if (target.classList.contains('cart-popover')) {
    closeCart();
  }
});

